import { useEffect } from 'react'

export default function useClickOutsideHandler (ref, callback, enabled = true) {
  useEffect(() => {
    if (enabled) {
      const handleClick = event => {
        const isInDocument = document.body.contains(event.target)
        const isInWrapper = ref.current.contains(event.target)
        if (isInDocument && !isInWrapper) {
          callback()
        }
      }

      document.body.addEventListener('click', handleClick)
      return () => document.body.removeEventListener('click', handleClick)
    }
  }, [ref, enabled, callback])
}
