import cloneDeep from 'lodash/cloneDeep'
import filter from 'lodash/filter'
import get from 'lodash/get'
import isEmpty from 'lodash/isEmpty'
import keyBy from 'lodash/keyBy'
import { useContext, useEffect, useMemo, useState } from 'react'

import {
  behaviorOverlays,
  mergeBehaviorOverlays
} from 'zmk-data/behaviors'
import localeCatalog from 'zmk-data/data/locales/catalog.json'

import { DefinitionsContext } from '../Contexts/Definitions'
import { SearchContext, SettingsContext } from '../providers'
import { getMergedGlyphs, normalizeZmkKeycodes } from '../keycodes'
import { resolveBehaviorParams } from 'zmk-data/parameters'

function useLocaleDefinitions (keymap) {
  const [loadedLocales, setLoadedLocales] = useState([])
  const locales = useMemo(() => {
    return keymap.headers.reduce((acc, header) => {
      if (header in localeCatalog) {
        acc.push(localeCatalog[header])
      }
      return acc
    }, [])
  }, [keymap.headers])

  useEffect(() => {
    (async function loadLocales () {
      const loaded = []
      for (const locale of locales) {
        // https://stackoverflow.com/a/62012224
        const data = await import(
          /* webpackInclude: /\.json$/ */
          /* webpackMode: "lazy" */
          /* webpackChunkName: "locales/[request]-keycodes" */
          `zmk-data/data/locales/${locale.filename}`
        )
        loaded.push(...normalizeZmkKeycodes(data.keys))
      }

      setLoadedLocales(loaded)
    })()
  }, [locales])

  return loadedLocales
}

function Context ({ children, ...props }) {
  const { keymap } = props
  const { keycodes, behaviours, glyphs } = useContext(DefinitionsContext)
  const { settings } = useContext(SettingsContext)
  const localeKeycodes = useLocaleDefinitions(keymap)

  const mergedGlyphs = useMemo(() => {
    const isMac = window.navigator.platform.startsWith('Mac')
    const isWin = window.navigator.platform.startsWith('Win')
    const platform = (isMac && 'mac') || (isWin && 'win')

    return getMergedGlyphs(glyphs, {
      unicode: get(settings, 'unicodeGlyphs', true),
      platform: get(settings, 'platformGlyphs', 'auto') !== 'auto'
        ? settings.platformGlyphs
        : platform
    })
  }, [glyphs, settings])

  const extendedBehaviours = useMemo(() => {
    const customBehaviors = keymap.behaviors.map(behavior => {
      behavior = cloneDeep(behavior)
      behavior.code = `&${behavior.labels[0]}`
      behavior.compatible = behavior.properties.compatible.parsed
      return behavior
    })
    mergeBehaviorOverlays(customBehaviors, behaviorOverlays)
    const fullList = [
      ...behaviours.map(behavior => ({
        ...behavior,
        description: behavior.description || behavior.schema?.description
      })),
      ...(customBehaviors || []).map(behavior => ({
        code: `&${behavior.labels[0]}`,
        bind: `&${behavior.labels[0]}`,
        name: behavior.name,
        displayName: behavior.properties.label?.raw || behavior.name,
        description: `Custom behavior: ${behavior.properties.label?.raw || behavior.name}`,
        context: behavior.context
      })),
      ...keymap.macros.map(macro => ({
        code: `&${macro.labels[0]}`,
        name: macro.name,
        description: `Custom macro ${macro.properties.label?.raw || macro.name}`
      }))
    ]

    // TODO: at this point we're just duplicating work that was done at a higher
    // level. It still needs to happen here because of the extended list of
    // keyboard-specific behaviours, so the App can just pass the list without
    // doing its own indexing.
    fullList.indexed = keyBy(fullList, 'code')
    fullList.context = {
      none: fullList.filter(({ context }) => !context),
      macros: filter(fullList, { context: 'macros' }),
      encoders: filter(fullList, { context: 'encoders' })
    }
    fullList.byContext = {
      none: keyBy(fullList.context.none, 'code'),
      macros: keyBy(fullList.context.macros, 'code'),
      encoders: keyBy(fullList.context.encoders, 'code')
    }

    return fullList
  }, [behaviours, keymap.behaviors, keymap.macros])

  const behaviorParameters = useMemo(() => {
    return resolveBehaviorParams([
      ...behaviours,
      ...keymap.behaviors,
      ...keymap.macros
    ])
  }, [behaviours, keymap.behaviors, keymap.macros])

  const availableLayers = useMemo(() => isEmpty(keymap)
    ? []
    : (
        keymap.layers.map((layer, i) => ({
          code: i,
          description: layer.name || `Layer ${i}`
        }))
      ), [keymap])

  const keycodesList = useMemo(() => {
    return [
      ...keycodes.map(keycode => cloneDeep(keycode)),
      ...localeKeycodes.map(keycode => cloneDeep(keycode))
    ]
  }, [keycodes, localeKeycodes])

  const keycodesIndexed = useMemo(() => {
    const indexed = keyBy(keycodesList, 'code')
    for (const name in mergedGlyphs) {
      const glyph = mergedGlyphs[name]
      const keycode = indexed[name]
      for (const alias of keycode.aliases) {
        indexed[alias].symbol = glyph.symbol || indexed[alias].symbol
        indexed[alias].faIcon = glyph.faIcon || indexed[alias].faIcon
      }
    }

    // last minute transform, the following keys are all implicitly shifted
    // TODO: don't do this here
    const shiftedKeys = [
      'EXCL',
      'AT',
      'HASH',
      'DLLR',
      'PRCNT',
      'CARET',
      'AMPS',
      'STAR',
      'UNDER',
      'PLUS',
      'PIPE',
      'TILDE2',
      'COLON',
      'TILDE',
      'LT',
      'QMARK',
      'CLEAR2',
      'PIPE2',
      'LPAR',
      'RPAR',
      'LBRC',
      'RBRC',
      'DQT',
      'GT'
    ]

    for (const key of shiftedKeys) {
      indexed[key].modifiers = ['LS']
    }

    return indexed
  }, [keycodesList, mergedGlyphs])

  // Extend ZMK definitions (keycodes and behaviours) with additional
  // definitions specific to this keyboard. This includes a list of layers for
  // layer-related behaviours and in the future can include custom behaviours
  // defined in the keymap and potentially preprocessor definitions for psuedo
  // layer names or aliased key codes.
  const sources = useMemo(() => ({
    code: keycodesIndexed,
    behaviours: extendedBehaviours.indexed,
    behavioursByContext: extendedBehaviours.byContext,
    layer: keyBy(availableLayers, 'code'),
    defines: keymap.defines
  }), [keycodesIndexed, extendedBehaviours, availableLayers, keymap.defines])

  const searchTargets = useMemo(() => {
    return {
      behaviour: extendedBehaviours,
      code: keycodesList
    }
  }, [extendedBehaviours, keycodesList])

  const getSearchTargets = useMemo(() => function (param, behaviour, context = 'none') {
    // Special case for behaviour commands which can dynamically add another
    // parameter that isn't defined at the root level of the behaviour.
    // Currently this is just `&bt BT_SEL` and is only represented as an enum.
    if (param.enum) {
      return param.enum.map(option => (
        typeof option === 'object'
          ? option
          : { code: option }
      ))
    }

    if (param.raw) {
      return []
    }

    if (!searchTargets[param]) {
      console.error('cannot find target for', param)
    }

    if (param === 'behaviour') {
      return searchTargets.behaviour.context[context]
    }

    return searchTargets[param]
  }, [searchTargets])

  const identifiers = useMemo(() => {
    return [
      ...Object.keys(keycodesIndexed),
      ...Object.keys(keymap.defines || {})
    ]
  }, [keycodesIndexed, keymap.defines])

  return (
    <SearchContext.Provider value={{ behaviorParameters, getSearchTargets, mergedGlyphs, sources, identifiers }}>
      {children}
    </SearchContext.Provider>
  )
}

export default Context
