import { useCallback, useState } from 'react'

export default function useToggle (initialState) {
  const [toggleState, setToggleState] = useState(initialState)
  const toggle = useCallback(() => setToggleState(state => !state), [setToggleState])
  const forceOn = useCallback(() => setToggleState(true), [setToggleState])
  const forceOff = useCallback(() => setToggleState(false), [setToggleState])
  return [toggleState, toggle, forceOn, forceOff]
}
