import compact from 'lodash/compact'
import * as config from '../config'

export const sourceChoices = compact([
  config.enableDemo && {
    id: 'demo',
    name: 'Demo Keyboard',
    description: 'Play around with a sample keymap'
  },
  config.enableLocal && {
    id: 'local',
    name: 'Local',
    description: 'Hello, me',
    collapsible: false
  },
  config.enableGitHub && {
    id: 'github',
    name: 'GitHub',
    description: 'Integrate with a GitHub repo, leveraging GitHub actions to compile firmware',
    collapsible: true
  },
  config.enableClipboard && {
    id: 'clipboard',
    name: 'Clipboard',
    description: 'Read and write keymap data directly from your clipboard',
    isBeta: true
  },
  config.enableFileSystem && window.showOpenFilePicker && {
    id: 'filesystem',
    name: 'File System',
    description: 'Read and write keymap files on your local filesystem',
    isBeta: true
  }
])

const selectedSource = localStorage.getItem('selectedSource')
const onlySource = sourceChoices.length === 1 ? sourceChoices[0].id : null

export const defaultSource = onlySource || (
  sourceChoices.find(source => source.id === selectedSource)
    ? selectedSource
    : sourceChoices[0].id
)
