import find from 'lodash/find'
import get from 'lodash/get'
import pick from 'lodash/pick'

export function stripLegacyProperties (keymap) {
  keymap.layers = keymap.layers.map(layer => (
    pick(layer, ['index', 'name', 'labels', 'properties'])
  ))

  keymap.combos = keymap.combos.map(combo => (
    pick(combo, ['index', 'name', 'labels', 'properties'])
  ))

  keymap.behaviors = keymap.behaviors.map(behavior => (
    pick(behavior, ['index', 'name', 'labels', 'properties', 'params'])
  ))

  keymap.macros = keymap.macros.map(macro => (
    pick(macro, ['index', 'name', 'labels', 'properties'])
  ))
}

export function getBindingSlots (binding, behaviorParameters) {
  const { value, params } = binding
  const base = behaviorParameters[value] || []
  const additional = getAdditionalParams(params, base)
  return [...base, ...additional]
}

function getAdditionalParams (parsedParams, slots) {
  const firstParsedParam = get(parsedParams, '[0]', {})
  const firstSlot = get(slots, '[0]')

  if (firstSlot?.enum) {
    const match = { code: firstParsedParam.value }
    const source = find(firstSlot.enum, match)
    if (source?.additionalParams) {
      return source.additionalParams
    }
  }

  return []
}
